const refs = {
    menuIcon: document.querySelector('.js-burger'),
    menuOverlay: document.querySelector('.js-overlay'),
    closeIcon: document.querySelector('.js-close'),
}

refs.menuIcon.addEventListener('click', handleBurgerClick);
refs.closeIcon.addEventListener('click', handleCloseClick);

function handleBurgerClick() {
    openMenu();
}

function handleCloseClick() {
    closeMenu();
}

function openMenu() {
    refs.menuOverlay.classList.remove('hidden');
}

function closeMenu() {
    refs.menuOverlay.classList.add('hidden')
}